<template>
  <div class="camera-test">
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="name" label="Name" width="300" align="center"></el-table-column>
      <el-table-column prop="status" label="Status" width="180" align="center"></el-table-column>
      <el-table-column prop="camera" label="Camera" width="180" align="center"></el-table-column>
      <el-table-column prop="date" label="Date" width="180" align="center"></el-table-column>
      <el-table-column label="Actions" align="center">
        <template>
          <div class="image-upload">
            <label for="file-input">
              <img src="https://img.icons8.com/windows/96/000000/camera.png" />
            </label>

            <input
              id="file-input"
              type="file"
              accept="image/*"
              capture
              @change="handlePreviewImage($event)"
              ref="fileInput"
            />
          </div>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      id="previewImageDialog"
      title="Preview Image"
      :visible.sync="dialogVisible"
      width="30%"
      @close="closeDialog"
    >
      <!-- <span>This is a preview image</span> -->
      <img v-if="previewUrl" :src="previewUrl" />
      <span slot="footer" class="dialog-footer">
        <el-button type="danger" @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="handleConfirm">Confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      previewUrl: null,
      dialogVisible: false,
      tableData: [
        {
          name: '〇〇銀行　預金通帳　２回目',
          status: '未登録',
          camera: ' 写真を送る',
          date: '2019-10-10 10:10',
          url: null
        },
        {
          name: '〇〇銀行　預金通帳',
          status: 'レガシィ確認中',
          camera: ' 写真を送る',
          date: '2019-10-10 10:10',
          url: null
        },
        {
          name: 'XX銀行　預金通帳',
          status: 'レガシィ確認完了',
          camera: ' 写真を送る',
          date: '2019-10-10 10:10',
          url: null
        }
      ]
    }
  },
  methods: {
    handlePreviewImage (e) {
      const file = e.target.files[0]
      this.previewUrl = URL.createObjectURL(file)
      // this.openPreviewImageDialog(this.previewUrl)
      this.$store.dispatch('setPreviewFile', file)
      this.$router.push({ name: 'CameraTestPreview' })
    },
    handleConfirm () {
      alert('confirm ...')
      this.dialogVisible = false
    },
    closeDialog () {
      this.reset()
    },
    openPreviewImageDialog (url) {
      this.dialogVisible = true
      this.previewUrl = url
    },
    reset () {
      const input = this.$refs.fileInput
      input.type = 'text'
      input.type = 'file'
      this.previewUrl = null
    }
  }
}
</script>

<style>
.image-upload > input {
  display: none;
}

.image-upload img {
  width: 60px;
  cursor: pointer;
}

#previewImageDialog {
  display: flex;
  justify-content: center;
  align-items: center;
}

#previewImageDialog img {
  max-width: 100%;
  max-height: 500px;
}
</style>
